.navbar {
    display: flex;
    height: 5rem;
    background-color: lightslategray;
    align-items: center;
    justify-items: start;
}

.navbar-logo {
    height: 5rem;
    width: 5rem;
}

.navbar-button {
    background-color: darkslategray;
    color: azure;
    border-radius: 5px;
    margin: 0.5rem;
    padding: 0.5rem; 
}

.navbar-button:hover {
    background-color: lightslategray;
    color: cyan;
    cursor: pointer;    
}
